<template>
    <div class="home">
        <p>{{murmur}}</p>
    </div>
</template>

<script>
    // @ is an alias to /src
    // import HelloWorld from '@/components/HelloWorld.vue'
    // import Nav from '@/components/nav.vue'
    import {
        createNamespacedHelpers
    } from 'vuex'
    const {
        mapState,
        mapActions
    } = createNamespacedHelpers('account')

    import Fingerprint2 from 'fingerprintjs2'
    export default {
        name: 'home',
        
        computed: {
            ...mapState({
                murmur(state) {
                    //window.console.log('state', state)
                    return state.did
                }
            })
        },
        methods: {
            ...mapActions(['act_update_did'])
        },
        components: {
            //Nav
        },
        created: function() {
            window.console.log("ww", this.murmur, this.murmur != "", this.$store.state.account.did)
            if (this.murmur != "") {
                return
            }
            window.console.log(this.murmur, 22, window.requestIdleCallback)
            var thisx = this
            if (window.requestIdleCallback) {
                requestIdleCallback(function() {
                    Fingerprint2.get(function(components) {
                        // an array of components: {key: ..., value: ...}
                        const values = components.map(function(component) {
                            return component.value
                        });
                        //window.console.log(thisx)
                        // 指纹
                        var murmur = Fingerprint2.x64hash128(values.join(''), 31);
                        thisx.act_update_did(murmur)
                        //window.console.log(thisx.murmur)
                    })
                })
            } else {
                setTimeout(function() {
                    Fingerprint2.get(function(components) {
                        const values = components.map(function(component) {
                            return component.value
                        });
                        //window.console.log(thisx)
                        // 指纹
                        var murmur = Fingerprint2.x64hash128(values.join(''), 31);
                        thisx.act_update_did(murmur)
                    })
                }, 500)
            }
            window.console.log(666, this.murmur)
        }
    }
</script>
