const state = {
    did: "",
    name: "",
    uid: 0,
}

// getters
const getters = {
    did: (state) => {
        return state.did
    }
}

// mutations
const mutations = {
    update_did(state, {
        did
    }) {
        state.did = did
    }
}

const actions = {
    act_update_did({
        commit
    }, did) {
        commit("update_did", {
            did: did
        })
    }
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}